@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:"Roboto",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}ul{list-style:none}a{text-decoration:none;color:#fff}.bg-main{background:#25dc99 !important}
.logo-container{width:8rem;height:2.3rem}.logo-container img{height:100%;width:100%;object-fit:contain}.navbar a{color:#fff !important}.btn.btn-outline-light:hover,.btn.btn-outline-light:active{color:#29276a !important}
.form-group label{font-size:.9rem}
.payment-option-card>div{border-bottom:1px solid #fff;padding:1rem 1rem}
.container{max-width:1340px !important}.burger-card{border:1px solid #d6d6d6;border-radius:5px;padding:10px 15px;text-align:center;font-weight:700;font-size:.8rem;cursor:pointer;color:#d6d6d6}.purple{color:#a7a2ee;border:1px solid #a7a2ee}.bg-purple{background-color:#29276a;color:#fff}.text-dark-purple{color:#605e8f}.btn-green{color:#fff;background-color:#25dc99;outline:none}.btn-green:active,.btn-green:not(:active),.btn-green:hover{color:#fff;background-color:#25dc99;outline:none}.light-purple{background:#edecfc;border:1px solid #edecfc;border-radius:9px;outline:none}.btn-purple{color:#fff;background-color:#29276a}.btn-purple:active,.btn-purple:hover{color:#fff}input:active,input:focus,select:active,select:focus{box-shadow:none !important;outline:none !important}.error-text{font-size:.8rem}
